body {
  padding-top: 50px;
}

select {
  width: 300px;
  height: 50px;
  color: orange;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  cursor: pointer;
  border: 3px solid orange;
  border-radius: 10px;
  outline: 3px solid orange;
  margin-left: 50px;
  padding-left: 20px;
  font-size: 20px;
  font-weight: 700;
}

option {
  color: orange;
  font-size: 20px;
  font-weight: 500;
}

.select-wrapper {
  width: fit-content;
  position: relative;
}

svg {
  width: 20px;
  height: 20px;
  fill: orange;
  pointer-events: none;
  transition: all .3s;
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translate(-50%, -50%);
}

.rotate {
  transform: translate(-50%, -50%)rotate(180deg);
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.cat-info {
  width: 500px;
  border: 5px solid orange;
  border-radius: 10px;
  margin: 0 auto;
  padding: 20px;
}

.img-wrapper {
  width: 500px;
  height: fit-content;
  border-radius: 10px;
  overflow: hidden;
}

.cat-img {
  width: 100%;
  transition: all .5s;
  transform: scale(1.2);
}

.cat-info:hover .cat-img {
  transform: scale(1);
}

.cat-breed {
  text-align: center;
  font-size: 25px;
  font-weight: 700;
}

.cat-description {
  text-indent: 30px;
  text-align: justify;
  line-height: 1.5em;
}

span {
  font-weight: 700;
}

.loader, .error {
  color: #0f610f;
  font-size: 30px;
  font-weight: 700;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.error {
  color: red;
}

